/*====================
   Animations
==========================*/

@keyframes pulseInOut {
  0% {
    opacity: 1;
    transform: scale(0.3);
  }
  100% {
    opacity: 0;
    transform: scale(1.7);
  }
}

@-webkit-keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
  }
}
@-moz-keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    -moz-transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -moz-transform: translate3d(0, 0, 0);
  }
}
@-o-keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    -o-transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -o-transform: translate3d(0, 0, 0);
  }
}
@keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

/*====================
	1. General code
==========================*/

html {
  width: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Mulish", sans-serif;
  margin: 0px;
  padding: 0px;
  font-size: 16px;
  line-height: 28px;
  overflow-x: hidden;
  font-weight: 400;
  color: #696393;
}

/*----------------------
	1.1. Typography
------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #042f3f;
  margin: 0 auto 20px;
  font-family: "Jost", sans-serif;
  font-weight: 600;
}
h1 {
  font-size: 80px;
}
h2 {
  font-size: 60px;
}
h3 {
  font-size: 40px;
}
h4 {
  font-size: 30px;
}
h5 {
  font-size: 26px;
}
h6 {
  font-size: 20px;
}
p {
  font-size: 16px;
  line-height: 28px;
  margin-bottom: 25px;
}
a {
  transition-duration: 0.5s;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  -webkit-transition-duration: 0.5s;
  -ms-transition-duration: 0.5s;
  text-decoration: none;
  color: #aa393f;
}
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
  color: #b7262d;
}
ul,
ol {
  margin: 0 0 20px;
  list-style-type: none;
}
ul li,
ol li {
  margin: 0 0 10px;
}
blockquote {
  background: #151525;
  position: relative;
  padding: 30px 30px 30px 80px;
  margin: 0 auto 30px;
}
blockquote::after {
  position: absolute;
  top: 40%;
  left: 25px;
  color: #4a5462;
  content: "\f10d";
  font-family: fontawesome;
  width: 40px;
  height: 40px;
  text-align: center;
  transform: translateY(-50%);
  font-size: 30px;
}
blockquote p {
  color: #bdc4cf;
  font-size: 14px;
  line-height: 24px;
  margin: 0 auto 15px;
}
blockquote .quote_owner {
  color: #aa393f;
  font-weight: 500;
  letter-spacing: 0.5px;
  font-size: 15px;
}
img {
  max-width: 100%;
  width: auto;
  height: auto;
}

.bg-overlay {
  position: relative;
  z-index: 1;
}

.bg-overlay::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -1;
}
.light-overlay:before {
  background-color: rgba(255, 255, 255, 0.6);
}

.bg-cover {
  background-size: cover;
}

.bg-center {
  background-position: center;
}

/*-----------------
	1.2. Button
-------------------------*/
.btn {
  position: relative;
  overflow: hidden;
  background: #aa393f;
  border: medium none;
  border-radius: 30px;
  color: #fff;
  font-family: "Jost", sans-serif;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 30px;
  height: auto;
  margin: auto;
  letter-spacing: 0.5px;
  padding: 15px 50px;
  z-index: 1;
  transition: all 0.3s linear 0s;
  -moz-transition: all 0.3s linear 0s;
  -o-transition: all 0.3s linear 0s;
  -webkit-transition: all 0.3s linear 0s;
  -ms-transition: all 0.3s linear 0s;
}

.btn:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  height: 0;
  width: 0;
  border-radius: 30px;
  background-color: #b7262d;
  z-index: -1;
  transition: 0.3s;
}
.light-bg {
  background: #042f3f;
  color: #fff;
}

.light-bg:before {
  background-color: #0f7398;
}
.btn.btn-lg {
  font-size: 14px;
  line-height: 30px;
  padding: 16px 65px;
}
.btn.btn-sm {
  font-size: 14px;
  padding: 8px 22px;
}
.btn:hover,
.btn:focus {
  color: #ffffff;
  outline: none;
  box-shadow: none;
}

.btn:hover:before {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.btn-link {
  font-weight: 600;
  font-size: 14px;
  color: #042f3f;
  text-transform: uppercase;
  letter-spacing: 0.6px;
}
.btn-link:hover {
  color: #aa393f;
}
.btn.outline-btn {
  background: none;
  color: #aa393f;
  border: #aa393f solid 3px;
  letter-spacing: 4px;
  padding: 15px 50px;
  font-weight: 700;
}
.btn.outline-btn:hover {
  color: #fff;
}

