/*-----------------
	1.4. Form
---------------------------*/
.form-group {
  margin-bottom: 25px;
  position: relative;
}
.form-label {
  font-weight: 500;
  margin: 0 auto 8px;
}
.form-control {
  background: #f7f7f7;
  border: 0 none;
  border-radius: 30px;
  box-shadow: none;
  color: #9d97c5;
  font-size: 13px;
  min-height: 63px;
  line-height: 33px;
  padding: 15px 30px;
}
.form-control:hover {
  box-shadow: none;
  outline: none;
}
.form-control:focus {
  box-shadow: none;
  border-color: #eedab3;
  outline: none;
  background: #efefef;
}
.select {
  position: relative;
}
.select select {
  appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
}
.select::after {
  color: #878787;
  content: "";
  cursor: pointer;
  font-family: fontawesome;
  font-size: 15px;
  padding: 12px 0;
  pointer-events: none;
  position: absolute;
  right: 15px;
  top: 0;
}
.form-control option {
  padding: 10px;
}
.control-label {
  color: #555;
  font-size: 15px;
  font-weight: 700;
}
.radio label,
.checkbox label {
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  padding-left: 30px;
  position: relative;
}
.radio input[type="radio"],
.checkbox input[type="checkbox"] {
  display: none;
}
.radio label:before {
  background-color: transparent;
  border-style: solid;
  border-width: 1px;
  border-color: #cacaca;
  border-radius: 50%;
  content: "";
  display: inline-block;
  height: 20px;
  left: 0;
  top: 6px;
  position: absolute;
  width: 20px;
}
.checkbox label::before {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #111111;
  content: "";
  display: inline-block;
  height: 16px;
  left: 0;
  position: absolute;
  top: 4px;
  width: 16px;
}
.radio input[type="radio"]:checked + label:before {
  content: "\2022";
  font-size: 30px;
  text-align: center;
  line-height: 11px;
  color: #aa393f;
}
.checkbox input[type="checkbox"]:checked + label:before {
  content: "\2713";
  font-size: 12px;
  text-align: center;
  line-height: 14px;
}
.black_input .form-control {
  background: #222;
  border-radius: 3px;
  color: #fff;
  border: #222 solid 1px;
  font-size: 17px;
}
.checkbox,
.radio {
  padding-top: 5px;
}

/* Input Groups */
.input-group-prepend,
.input-group-append {
  margin: 0;
}

.input-group-prepend .btn {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  box-shadow: none;
}

.input-group .form-control:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.input-group .form-control:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.input-group-append .btn {
  margin: 0;
}

/*-----------------
	1.5. Table
---------------------------*/
table {
  margin: 0 0 30px;
  width: 100%;
}
table th,
table td {
  border: 1px solid #cccccc;
  padding: 15px;
  padding: 18px;
}
table th img,
table td img {
  max-width: 100%;
}
table thead {
  background: #eee;
}
table thead th,
table thead td {
  text-transform: uppercase;
  font-weight: 900;
  color: #111;
}

/*-----------------
	1.6. Accordion
---------------------------*/
.panel-title {
  position: relative;
}
.panel-title a {
  display: block;
  padding: 28px 50px 28px 0;
  position: relative;
}
.panel-title a::after {
  position: absolute;
  top: 33px;
  right: 0;
  background: #b7b7b7;
  height: 2px;
  width: 16px;
  content: "";
}
.panel-title a.collapsed::before {
  position: absolute;
  top: 26px;
  right: 7px;
  background: #b7b7b7;
  height: 16px;
  width: 2px;
  content: "";
}
.panel-title {
  margin: 0 auto;
}
.panel.panel-default:first-child {
  border-top: #e8e7e7 solid 1px;
}
.panel.panel-default {
  border-bottom: #e8e7e7 solid 1px;
}

/*-----------------------------------------------------------
	1.7. Section-background-color & sectino-heading
-------------------------------------------------------------------*/
.primary-bg {
  background: #aa393f;
}
.secondary-bg {
  background: #042f3f;
}
.gray_bg {
  background: #f3f3f3;
}
.section-padding {
  padding: 120px 0;
}

.section-padding-2 {
  padding: 120px 0 90px;
}
.section-header {
  margin: 0 auto;
  max-width: 620px;
  padding-bottom: 30px;
}
.section-header.full-wd {
  max-width: 1000px;
}
.section-header h2 {
  margin: 0 auto 30px;
  letter-spacing: -1px;
}
.section-header .heading_arrow {
  margin-bottom: 10px;
}
.section-header h5 {
  color: #746f96;
  font-size: 24px;
  text-transform: uppercase;
}

.section-header.flex-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  max-width: 100%;
}
.section-header.flex-header h3 {
  margin: 0;
}
.z_index {
  z-index: 2;
}
.divider {
  border-top: #ebebeb solid 1px;
}
.white_text,
.white_text p,
.white_text h1,
.white_text h2,
.white_text h3,
.white_text h4,
.white_text h5,
.white_text h6 {
  color: #fff;
}

/*--------------------
	1.8. Tabs
------------------------------*/
.nav.nav-tabs {
  border: medium none;
}
.nav-tabs > li a {
  background: #000000 none repeat scroll 0 0;
  border-radius: 0;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
  margin-right: 5px;
  padding: 14px 26px;
  text-align: center;
}
.nav-tabs > li.active > a,
.nav-tabs > li.active > a:focus,
.nav-tabs > li.active > a:hover,
.nav-tabs > li a:hover,
.nav-tabs > li a:focus {
  color: #ffffff;
  background: #aa393f;
}
.nav-tabs > li {
  margin-bottom: 0;
}

/*--------------------------------------------
	1.9. parallex-background
---------------------------------------------------*/
.parallex-bg {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

/*------------------------
	1.10. Modal
--------------------------------*/
.modal-content {
  border: 0;
  border-radius: 0;
}

.modal-body {
  padding: 55px 40px;
}

.modal-lg {
  max-width: 1000px;
}

.modal-body .sigma_close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 3;
}
label.error {
  color: #ff0000;
  display: block;
  font-size: 13px;
  font-weight: normal;
  text-align: left;
}

.sigma_close {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #e1e1e1;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s;
}

.sigma_close:hover {
  border-color: #aa393f;
  background-color: #aa393f;
  transform: rotate(90deg);
  outline: none;
}

.sigma_close span {
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #696393;
}

.sigma_close:hover span {
  background-color: #fff;
}

.sigma_close span:first-child {
  transform: rotate(45deg);
}
.sigma_close span:last-child {
  transform: rotate(135deg);
}

/*------------------------
	1.11. Slick
--------------------------------*/

.slick-slide {
  position: relative;
  outline: none;
}

.slick-prev,
.slick-next {
  position: relative;
  font-size: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  color: #042f3f;
  border: 0;
  padding: 0;
  outline: none;
  transition: 0.3s;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background-color: #042f3f;
  color: #fff;
}

.slick-prev:before,
.slick-next:before {
  content: "\f053";
  font-family: "FontAwesome";
  font-size: 16px;
  font-weight: 400;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.slick-next:before {
  content: "\f054";
}
.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.slick-dots li:focus,
.slick-dots li:hover,
.slick-dots li button:focus,
.slick-dots li button:hover {
  outline: none;
}

.slick-dots li {
  margin-bottom: 0;
}

.slick-dots li + li {
  margin-left: 10px;
}

.slick-dots li button {
  font-size: 0;
  width: 12px;
  height: 12px;
  background-color: #fff;
  border-radius: 50%;
  transition: 0.3s;
  border: 0;
  padding: 0;
  cursor: pointer;
}

.slick-dots .slick-active button {
  background-color: #042f3f;
}

/*--- custom arrows ---- */

.sigma_custom-arrows {
  display: flex;
  align-items: center;
}

.sigma_custom-arrows .slick-prev,
.sigma_custom-arrows .slick-next {
  background-color: #f0f2f3;
  opacity: 1;
  visibility: visible;
}

.sigma_custom-arrows .slick-prev:hover,
.sigma_custom-arrows .slick-next:hover {
  background-color: #042f3f;
}

.sigma_custom-arrows .slick-next {
  margin-left: 12px;
}

/*================
	 2. Header
===========================*/

.slide_menu {
  position: fixed;
  top: 0;
  padding: 20px 0;
  right: -400px;
  width: 400px;
  height: 100%;
  background: #fff;
  overflow-y: auto;
  z-index: 9999;
  transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
}
.sidebar_logo {
  margin: 0 auto 30px;
  padding: 20px;
  text-align: center;
}
.slide_menu.show {
  right: 0;
}

.slide_menu .navbar-nav li a {
  padding: 15px 22px;
}
.slide_menu ul li ul.sub-menu {
  position: relative;
}

.slide_menu .sidebar_widget {
  padding: 0 30px;
}

.slide_menu .sidebar_widget + .sidebar_widget {
  margin-top: 30px;
}

#overlay_bg.show {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: rgba(0, 0, 0, 0.6);
  transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  -webkit-transition-duration: 0.3s;
}

