/*==============
	20. Custom Map
=====================*/
.map-markers {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.map-marker {
  position: relative;
  cursor: pointer;
}
.map-marker::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
}
.map-marker > span {
  width: 10px;
  display: block;
  transition: 0.3s;
  height: 10px;
  background-color: #aa393f;
  border-radius: 50%;
  position: relative;
}
.map-marker > span::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-left: -10px;
  margin-top: -10px;
  background-color: #aa393f;
  animation-name: pulseInOut;
  opacity: 0;
  animation-duration: 3s;
  animation-iteration-count: infinite;
}
.map-marker-content {
  position: absolute;
  top: -140px;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.3s;
  opacity: 0;
  min-width: 300px;
  visibility: hidden;
  padding: 10px;
  background-color: #042f3f;
  color: #fff;
  font-size: 12px;
  z-index: 3;
}
.map-marker-content::before {
  content: "";
  position: absolute;
  bottom: -15px;
  width: 0;
  left: 48%;
  transform: translateX(-50%);
  transform: rotate(270deg);
  height: 0;
  border-top: 10px solid transparent;
  border-right: 10px solid #042f3f;
  border-bottom: 10px solid transparent;
}
.map-marker-content p {
  color: #fff;
  margin: 0 0 5px;
  font-size: 12px;
}
.map-marker.active .map-marker-content {
  opacity: 1;
  visibility: visible;
  top: -130px;
}

.map-marker.active > span {
  box-shadow: 0px 10px 50px 0px rgba(53, 82, 99, 0.09);
  transform: scale(1.1);
}

.map-marker.marker-1 {
  position: absolute;
  top: 40%;
  right: 40%;
}
.map-marker.marker-2 {
  position: absolute;
  top: 30%;
  right: 20%;
}
.map-marker.marker-3 {
  position: absolute;
  top: 45%;
  right: 50%;
}
.map-marker.marker-4 {
  position: absolute;
  top: 20%;
  right: 85%;
}
.map-marker.marker-5 {
  position: absolute;
  top: 60%;
  right: 70%;
}
.map-marker.marker-6 {
  position: absolute;
  top: 20%;
  right: 30%;
}

