}

.sigma_product .sigma_product-controls {
  position: absolute;
  right: 20px;
  top: 20px;
  transition: 0.3s;
}

.sigma_product .sigma_product-controls a {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #042f3f;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  right: -10px;
