  visibility: visible;
  right: 0;
}

.sigma_product:hover .sigma_product-controls a:nth-child(2) {
  transition: right 1s, background-color 0.3s, color 0.3s;
}
.sigma_product:hover .sigma_product-controls a:last-child {
